export default function Dashboard() {
  const[AAPLPrice, setAAPLPrice]=useState(0);
  const[GOOGPrice, setGOOGPrice]=useState(0);
  const[METAPrice, setMETAPrice]=useState(0);
  const[MSFTPrice, setMSFTPrice]=useState(0);
  const[TSLAPrice, setTSLAPrice]=useState(0);
  const[AMZNPrice, setAMZNPrice]=useState(0);
  const[NVDAPrice, setNVDAPrice]=useState(0);
  const[ADBEPrice, setADBEPrice]=useState(0);
  const[sec,setSec]=useState(0);

  const context = useContext(VTAContext);
 const {putdata,getdata} = context;
//  console.log("PUTDATA:",getdata);

  async function CurrPrices(tickers)
 {
      //console.log(tickers[0]);
      await fetch('https://api.twelvedata.com/price?symbol=' + tickers.toString() + '&apikey=' + TDKEY)
  .then(response => response.json())
  .then(data => {
    console.log(data);
    //console.log(data["AAPL"]["price"]);
    //let temp2=;
    setAAPLPrice(parseFloat(data["AAPL"]["price"]));
    setGOOGPrice(parseFloat(data["GOOG"]["price"]));
    setAMZNPrice(parseFloat(data["AMZN"]["price"]));
    setMSFTPrice(parseFloat(data["MSFT"]["price"]));
    setMETAPrice(parseFloat(data["META"]["price"]));
    setTSLAPrice(parseFloat(data["TSLA"]["price"]));
    setNVDAPrice(parseFloat(data["NVDA"]["price"]));
    setADBEPrice(parseFloat(data["ADBE"]["price"]));
   //console.log(temp);
  setSec(sec+1);
 }
  )
  .catch(error => console.error('Error:', error));
 }

  useEffect(() => {

    const fetchData = async () => {
      
      await CurrPrices(["AAPL","GOOG","META","MSFT","AMZN","TSLA","NVDA","ADBE"]);
            //await CurrPrices(["AAPL","GOOG"]);

    };
  
    fetchData();
    console.log("Dashboard Useeffect---------");
  
    // const interval = setInterval(fetchData, 60000);
    // This is the cleanup function where we clear our interval when the component unmounts
    //return () => clearInterval(interval);
  }, [AAPLPrice,sec]); // Empty dependency array ensures this runs once on mount and unmount

  // const putdata = () => {
  //   set(ref(db, 'users'), {
  //     id:1,
  //     name:"First",
  //     age:21,
  //     buyingprice:parseFloat(GOOGPrice)
  //   })

  //   alert("Stock bought");
  // }
  return <>
 <table className="table" id="main">
  <thead>
    <tr>
      <th scope="col">Stock</th>
      <th scope="col">Price</th>
    </tr>
  </thead>
  <tbody>
  <tr>
      <th scope="col">AAPL</th>
      <td>{AAPLPrice}</td>
    </tr>
    <tr>
      <th scope="col">GOOG</th>
      <td>{GOOGPrice}</td>
    </tr>
    <tr>
      <th scope="col">META</th>
      <td>{METAPrice}</td>
    </tr>
    <tr>
      <th scope="col">MSFT</th>
      <td>{MSFTPrice}</td>
    </tr>
    <tr>
      <th scope="col">AMZN</th>
      <td>{AMZNPrice}</td>
    </tr>
    <tr>
      <th scope="col">TSLA</th>
      <td>{TSLAPrice}</td>
    </tr>
    <tr>
      <th scope="col">NVDA</th>
      <td>{NVDAPrice}</td>
    </tr>
    <tr>
      <th scope="col">ADBE</th>
      <td>{ADBEPrice}</td>
    </tr>
  </tbody>
  <button onClick={(e)=> {
      let temp=AAPLPrice;
      console.log(temp);
      putdata({'Buying Price':temp, 'Stock name': 'AAPL', 'Selling Price' : 0});
  }}>PutData</button>
  <button onClick={(e)=> {
getdata();
}}>GetData</button>

  </table>
  
  </>;
}
